@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor{
    margin-left: 1rem;
    min-width: 30rem;
    @include m.tablet {
        margin: 0 auto;
    }
    
}

.apartado{
    margin: 1.5rem 2rem;
    padding: 1rem;
    text-align: center;
    background-color: lighten(v.$grisClaro, 8%);
    border-radius:1rem;
    &:nth-child(odd){
        background-color:v.$grisClaro;
    }


    

    h3{
        color: darken(v.$azul, 20%);
    }

    span{
        font-weight: bold;
        padding-top: 1rem;

    }

    ul{
        list-style: none;
    }

    .fecha{
        color: darken(v.$azul, 30%);
        font-weight: bold;
        
    }
        
    .icono-principal{
        visibility:hidden;
        display:none;

        @include m.tablet {
            display: inline-block;
            visibility:visible;
        }
    }

    .boton{
        @include m.boton (v.$morado, v.$grisClaro);
        @include m.tablet {
            display: inline-block;
            
        }
    }

    .imagen{
        display:none;
        @include m.tablet {
            display:inline-block;
            border: 5px solid v.$morado;
            border-radius: .5rem;
            height: auto;
            
            max-width: 600px;
            
        }
    }

    .navegacion{

        @include m.tablet {
            display: flex;
            justify-content: space-between;
            margin: 1rem 3rem;
            
        }
    }

    
}