@use 'variables' as v;
@use 'base/mixins' as m;

h1, h2, h3 {
    font-family: v.$fuente_principal;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
    color: v.$negro;
    text-align: center;
}

h1 {
    font-size: 6rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 2rem;
    text-align: center;
    @include m.tablet {
        font-size: 3rem;
    }
}
h4 {
    font-size : 1.8rem;
    @include m.tablet {
        font-size: 2.8rem;
    }
}

h5{
    font-size: 1.7rem;
    margin: 2.5rem auto 2rem auto;
    @include m.tablet {
        font-size:2.4rem;
    }
}
p,li {
    margin: 1rem auto;
    font-size: 1.5rem;
    @include m.tablet {
        font-size: 2rem;
    };
}
