/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  background-color: #FFFFFF;
  color: #1a1b15;
  min-height: 100%;
  text-align: center;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Fira Sans Condensed", sans-serif;
  font-size: 1.6rem;
  text-align: center;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  min-width: 32rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Fira Sans Condensed", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #1a1b15;
  text-align: center;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 2rem;
  text-align: center;
}
@media (min-width: 768px) {
  h3 {
    font-size: 3rem;
  }
}

h4 {
  font-size: 1.8rem;
}
@media (min-width: 768px) {
  h4 {
    font-size: 2.8rem;
  }
}

h5 {
  font-size: 1.7rem;
  margin: 2.5rem auto 2rem auto;
}
@media (min-width: 768px) {
  h5 {
    font-size: 2.4rem;
  }
}

p, li {
  margin: 1rem auto;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  p, li {
    font-size: 2rem;
  }
}

.contenedor {
  margin-left: 1rem;
  min-width: 30rem;
}
@media (min-width: 768px) {
  .contenedor {
    margin: 0 auto;
  }
}

.apartado {
  margin: 1.5rem 2rem;
  padding: 1rem;
  text-align: center;
  background-color: whitesmoke;
  border-radius: 1rem;
}
.apartado:nth-child(odd) {
  background-color: #e1e1e1;
}
.apartado h3 {
  color: #076493;
}
.apartado span {
  font-weight: bold;
  padding-top: 1rem;
}
.apartado ul {
  list-style: none;
}
.apartado .fecha {
  color: #054362;
  font-weight: bold;
}
.apartado .icono-principal {
  visibility: hidden;
  display: none;
}
@media (min-width: 768px) {
  .apartado .icono-principal {
    display: inline-block;
    visibility: visible;
  }
}
.apartado .boton {
  background-color: #57007f;
  padding: 1.5rem 4rem;
  color: #e1e1e1;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0.5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .apartado .boton {
    width: auto;
    font-size: 2rem;
  }
}
.apartado .boton:hover {
  background-color: #9d00e5;
  color: #fbfbfb;
  cursor: pointer;
}
@media (min-width: 768px) {
  .apartado .boton {
    display: inline-block;
  }
}
.apartado .imagen {
  display: none;
}
@media (min-width: 768px) {
  .apartado .imagen {
    display: inline-block;
    border: 5px solid #57007f;
    border-radius: 0.5rem;
    height: auto;
    max-width: 600px;
  }
}
@media (min-width: 768px) {
  .apartado .navegacion {
    display: flex;
    justify-content: space-between;
    margin: 1rem 3rem;
  }
}

.contenedor-header {
  margin: 2rem 2rem 0 2rem;
  min-width: 30rem;
  padding: 1rem;
  background: linear-gradient(to left, #FFFFFF, #0da6f3);
  box-shadow: -20px 15px 20px -13px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
}
.contenedor-header .cabecera h1, .contenedor-header .cabecera h2, .contenedor-header .cabecera h3 {
  color: #333333;
}
.contenedor-header .cabecera h2 {
  font-size: 2.2rem;
}
@media (min-width: 768px) {
  .contenedor-header .cabecera h2 {
    font-size: 3.2rem;
  }
}
.contenedor-header .cabecera h1 {
  font-size: 2.8rem;
}
@media (min-width: 768px) {
  .contenedor-header .cabecera h1 {
    font-size: 4.2rem;
  }
}
.contenedor-header .cabecera .titulo {
  display: flex;
  text-align: center;
  justify-content: space-around;
}
.contenedor-header .cabecera .titulo h3 {
  border-bottom: 1px, solid, #929292;
  margin-top: 2rem;
}

.contenedor-footer {
  margin: 2rem;
  min-width: 30rem;
  padding: 2rem 1rem;
  background: linear-gradient(to right, #FFFFFF, #0da6f3);
  box-shadow: 20px -15px 20px -13px rgba(0, 0, 0, 0.4);
  border-radius: 5px;
}
.contenedor-footer .menu-bar .boton-menu {
  margin: 2rem auto;
}
@media (min-width: 768px) {
  .contenedor-footer .correo-footer {
    padding: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.contenedor-footer h3, .contenedor-footer p, .contenedor-footer a {
  color: #333333;
}
.contenedor-footer h3 {
  margin-bottom: 1rem;
}
.contenedor-footer p {
  font-weight: bold;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .contenedor-footer p {
    line-height: 1rem;
    font-size: 2.2rem;
  }
}
.contenedor-footer a:hover {
  color: #737373;
}

.menu-bar {
  padding: 0 4rem;
}
@media (min-width: 768px) {
  .menu-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 4rem;
  }
}
.menu-bar .boton-menu {
  background-color: #000cbb;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0.5rem 0;
  border-radius: 1rem;
  font-weight: 400;
  padding: 1rem;
}
@media (min-width: 768px) {
  .menu-bar .boton-menu {
    width: auto;
    font-size: 2rem;
  }
}
.menu-bar .boton-menu:hover {
  background-color: #2230ff;
  color: white;
  cursor: pointer;
}

.modal {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.modal .cuerpo-modal {
  max-width: 60rem;
  background-color: #FFFFFF;
  padding: 2rem;
  margin: 0 auto;
  transform: translateY(-50rem);
  transition-property: all;
  transition-duration: 700ms;
  transition-timing-function: ease-in-out;
  opacity: 0;
  border-radius: 1rem;
  min-width: 30rem;
}
.modal .cuerpo-modal .cerrar-modal {
  background-color: #57007f;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 1.5rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 0.5rem 0;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .modal .cuerpo-modal .cerrar-modal {
    width: auto;
    font-size: 2rem;
  }
}
.modal .cuerpo-modal .cerrar-modal:hover {
  background-color: #9d00e5;
  color: white;
  cursor: pointer;
}
.modal .cuerpo-modal.animar {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  opacity: 1;
}
.modal .cuerpo-modal.cerrar {
  transform: translateY(-200%);
  opacity: 0;
}

.formacion {
  margin-bottom: 6rem;
}

@media (min-width: 768px) {
  .conocimientos ul {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.conocimientos p {
  font-weight: 600;
}

ul {
  padding: 0 2rem;
}