@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-footer{
    margin: 2rem;
    min-width: 30rem;
    padding: 2rem 1rem;
    background: linear-gradient(to right, v.$blanco, v.$azul);
    box-shadow: 20px -15px 20px -13px rgba(0, 0, 0, .4);
    border-radius: 5px;

    .menu-bar{
        .boton-menu{
            margin: 2rem auto;
        }
    }

    .correo-footer{

        
        @include m.tablet {
            padding: 2rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        
    }


    h3,p,a{
        color: v.$grisOscuro;
    }
    h3{
       margin-bottom: 1rem;

    }
    p{
        font-weight: bold;
        font-size:1.5rem;
        
        @include m.tablet {
            line-height: 1rem;
            font-size: 2.2rem;
        }
    }

    a{
        
        &:hover{
            color:lighten(v.$grisOscuro, 25%);
        }
    }
}

