@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    background-color: v.$blanco;
    color: v.$negro;
    min-height: 100%;
    text-align: center;
    
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    text-align: center;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    min-width: 32rem;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}
