@use 'base/mixins' as m;
@use 'base/variables' as v;

.contenedor-header{
    margin: 2rem 2rem 0 2rem;
    min-width: 30rem;
    padding: 1rem;
    background: linear-gradient(to left, v.$blanco, v.$azul);
    box-shadow: -20px 15px 20px -13px rgba(0, 0, 0, .4);
    border-radius: 5px;



.cabecera{

    h1, h2, h3{
        color:v.$grisOscuro;
        }

    h2{
        font-size: 2.2rem;
        @include m.tablet {
            font-size: 3.2rem;
        }
    }

    h1{
        font-size: 2.8rem;
        @include m.tablet {
            font-size: 4.2rem;
        }
    }
        
    .titulo{
        display: flex;
        text-align: center;
        justify-content: space-around;
        
        h3{
            border-bottom: 1px, solid, v.$gris;
            margin-top: 2rem;

            
        }      
        
    }

    
        
}
    
}
